import React, { Component } from "react";
import { FormGroup, FormControl, ControlLabel } from "react-bootstrap";
import LoaderButton from "../components/LoaderButton";
import config from "../config";
import "./NewNote.css";
import { API } from "aws-amplify";
import { s3Upload } from "../libs/awsLib";

export default class NewNote extends Component {
  constructor(props) {
    super(props);

    this.file = null;

    this.state = {
      isLoading: null,
      content: ""
    };
  }

  validateForm() {
    return this.state.content.length > 0;
  }

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  handleFileChange = event => {
    this.file = event.target.files[0];
  }

  handleSubmit = async event => {
    event.preventDefault();
  
    if (this.file && this.file.size > config.MAX_ATTACHMENT_SIZE) {
      alert(`Please pick a file smaller than ${config.MAX_ATTACHMENT_SIZE/1000000} MB.`);
      return;
    }
  
    this.setState({ isLoading: true });
  
    try {
      const attachment = this.file
        ? await s3Upload(this.file)
        : null;
  
      await this.createNote({
        attachment,
        content: this.state.content6,
        content: this.state.content1,
        content: this.state.content2,
        content: this.state.content3,
        content: this.state.content4,
        content: this.state.content5
      });
      this.props.history.push("/");
    } catch (e) {
      alert(e);
      this.setState({ isLoading: false });
    }
  }
  
  createNote(note) {
    return API.post("notes", "/notes", {
      body: note
    });
  }

  handleNumber(){
    const min = 10;
    const max = 99;
    const rand = min + Math.random() * (max - min);
    this.setState({ random: this.state.random + rand });
  }

  render() {
    return (
      <div className="NewNote">
        <form onSubmit={this.handleSubmit}>

          <h2> Create New Project </h2>
          <FormGroup controlId="content">
            <h4>Project name</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content6}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="content">
            <h4>Project Manager</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content1}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="content">
            <h4>Project Id</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content2}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="content">
            <h4>Start Date(DD/MM/YY)</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content3}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="content">
            <h4>End Date(DD/MM/YY)</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content4}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="content">
            <h4>Note</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content5}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="content">
            <h4>Responsed Developer</h4>
            <FormControl
              onChange={this.handleChange}
              value={this.state.content5}
              componentClass="textarea"
            />
          </FormGroup>

          <FormGroup controlId="file">
            <ControlLabel>Attachment</ControlLabel>
            <FormControl onChange={this.handleFileChange} type="file" />
          </FormGroup>

          <LoaderButton
            block
            bsStyle="primary"
            bsSize="large"
            disabled={!this.validateForm()}
            type="submit"
            isLoading={this.state.isLoading}
            text="Create"
            loadingText="Creating…"
          />

        
        </form>
      </div>
    );
  }
}